import { AppShell, TabbarContainer } from "@app/ui";
import { SettingsContent } from "@src/components/page/settings/content/Settings";
import { SettingsPageDataContextLoader } from "@src/components/page/settings/data/settings.context";

import { loadSettingsPageData } from "@src/components/page/settings/data/settings.data.intf";

export default function SettingsPage() {
    return (
        <AppShell>
            <TabbarContainer titleConf={{ ns: "homepage/pages/settings/content", key: "content.title" }}>
                <SettingsPageDataContextLoader value={loadSettingsPageData()}>
                    <SettingsContent />
                </SettingsPageDataContextLoader>
            </TabbarContainer>
        </AppShell>
    )
}   